<template>
    <div>
        <div id="map"></div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            map: null,
        };
    },

    mounted() {
        if (!window.kakao || !window.kakao.maps) {
            const script = document.createElement("script");
            script.src = `//dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=148b6ba374ec3bc5322801fd52a1e52d`;

            script.addEventListener("load", () => {
                kakao.maps.load(this.initMap);
            });
            document.head.appendChild(script);
        } else {
            //console.log("Ïù¥ÎØ∏ Î°úÎî©Îê®: ", window.kakao);
            this.initMap();
        }
    },

    methods: {
        initMap() {
            const container = document.getElementById("map");
            const options = {
                center: new kakao.maps.LatLng(
                    37.3081468108158,
                    126.8287756218405,
                    16
                ),
                level: 5,
            };
            this.map = new kakao.maps.Map(container, options);

            this.initMaker();
        },
        initMaker() {
            // ÎßàÏª§Í∞Ä ÌëúÏãú ÏúÑÏπò
            // const markerPosition = new window.kakao.maps.LatLng(
            //     37.3081468108158,
            //     126.8287756218405,
            //     16
            // );

            var imageSrc =
                    "https://user-images.githubusercontent.com/97419543/219056501-0a38eced-619f-44d2-8636-84f55d7d7e99.png", // ÎßàÏª§Ïù¥ÎØ∏ÏßÄÏùò Ï£ºÏÜåÏûÖÎãàÎã§
                imageSize = new kakao.maps.Size(64, 69), // ÎßàÏª§Ïù¥ÎØ∏ÏßÄÏùò ÌÅ¨Í∏∞ÏûÖÎãàÎã§
                imageOption = { offset: new kakao.maps.Point(27, 69) }; // ÎßàÏª§Ïù¥ÎØ∏ÏßÄÏùò ÏòµÏÖòÏûÖÎãàÎã§. ÎßàÏª§Ïùò Ï¢åÌëúÏôÄ ÏùºÏπòÏãúÌÇ¨ Ïù¥ÎØ∏ÏßÄ ÏïàÏóêÏÑúÏùò Ï¢åÌëúÎ•º ÏÑ§Ï†ïÌï©ÎãàÎã§.

            // ÎßàÏª§Ïùò Ïù¥ÎØ∏ÏßÄÏ†ïÎ≥¥Î•º Í∞ÄÏßÄÍ≥† ÏûàÎäî ÎßàÏª§Ïù¥ÎØ∏ÏßÄÎ•º ÏÉùÏÑ±Ìï©ÎãàÎã§
            var markerImage = new kakao.maps.MarkerImage(
                    imageSrc,
                    imageSize,
                    imageOption
                ),
                markerPosition = new kakao.maps.LatLng(
                    37.3081468108158,
                    126.8287756218405,
                    16
                ); // ÎßàÏª§Í∞Ä ÌëúÏãúÎê† ÏúÑÏπòÏûÖÎãàÎã§

            // ÎßàÏª§ ÏÉùÏÑ±
            const marker = new window.kakao.maps.Marker({
                position: markerPosition,
                image: markerImage, // ÎßàÏª§Ïù¥ÎØ∏ÏßÄ ÏÑ§Ï†ï
            });

            // ÎßàÏª§Í∞Ä ÏßÄÎèÑ ÏúÑÏóê ÌëúÏãúÎêòÎèÑÎ°ù ÏÑ§Ï†ï
            marker.setMap(this.map);
        },
    },
};
</script>

<style scoped>
#map {
    width: 100%;
    height: 30vh;
}
</style>
